*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body{

    font-family: var(--font-family);
    background-color: var(--white);
}

a{
    color:unset;
    text-decoration: none;
}

.section__padding{
    margin: 4rem 6rem;
}
.section__margin{
    margin: 4rem 6rem;
}
@media screen and (max-width: 700px){
    .section__padding{
        padding: 4rem;
    }
    .section__margin{
        padding: 4rem;
    }
}
@media screen and (max-width: 550px){
    .section__padding{
        padding: 4rem 2rem;
    }
    .section__margin{
        padding: 4rem 2rem;
    }
}